@charset "utf-8";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-size: 14px;
}
img{
  vertical-align: top;
}
ul,
li,
ol {
  list-style: none;
}
a {
  text-decoration: none;
  color: #333;
}
body {
  font-family: "맑은 고딕", sans-serif, arial;
  font-size: 16px;
  color: #333;
  box-sizing: border-box;
}
header,
footer,
main {
  /* width: 1050px; */
  margin: 0 auto;
  /* background: #0f0; */
}
header,
footer {
  width: 1050px;
}
footer {
  text-align: center;
  height: 200px;
}
footer > address {
  line-height: 200px;
  font-style: normal;
}
.img_wrap {
  vertical-align: top;
}
