@charset "UTF-8";
.product {
  width: 1050px;
  margin: 0 auto;
}
.product h2 {
  font-size: 24px;
  text-align: center;
}
.product > h2,
.product > ul > li > p {
  margin: 40px 0px;
}
.product > ul {
  margin: 0 auto;
  display: flex;
  /* flex-wrap: wrap; */
  gap: 20px;
  width: 1050px;
  /* width: 1420px; */
}
.product > ul > li {
  width: 25%;
}
.product > ul > li > a > p {
  width: 100%;
  position: relative;
}
.product > ul > li > a > p:first-child {
  height: 318.594px;
  overflow: hidden;
}
.product > ul > li > a > p > img:first-child {
  z-index: 3;
  position: absolute;
}
.cart_img {
  position: absolute;
  right: 20px;
  bottom: 20px;
}
.product li > a > p > img:last-child {
  transition: 0.5s;
  width: 100%;
}
.product li > a > p > img:last-child:hover {
  transform: scale(1.03);
}
.product li > a > p:nth-child(2) {
  margin: 10px 0px;
}
.product li > a > p:nth-child(3) {
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 18px;
}
.product li > a > p:last-child {
  font-size: 14px;
  color: #666 !important;
  /* font-weight: bold; */
}
.banner01 {
  width: 1050px;
  margin: 50px auto;
}
.banner01 > img {
  width: 100%;
}
#D_S {
  display: flex;
  width: 1050px;
  margin: 50px auto;
  gap: 20px;
  position: relative;
  justify-content: space-between;
}
#D_S > h2 {
  display: none;
}
#D_S > div {
  width: 25%;
}
#D_S > div > p:first-child {
  margin-bottom: 10px;
  font-size: 28px;
}
#D_S > div > p:nth-child(2) {
  color: #666;
}
#D_S > div > p:nth-child(3) {
  line-height: 84px;
  font-weight: bold;
  font-size: 36px;
}
#D_S > div > p:last-child {
  color: #cacaca;
  font-size: 14px;
}
#L_S {
  width: 75%;
  display: flex;
  gap: 20px;
}
#L_S > li {
  width: 33.3%;
}
#L_S > li > img {
  width: 100%;
}
#L_S > li > p:first-of-type {
  margin-top: 10px;
  font-size: 12px;
  color: #666;
}
#L_S > li > p {
  margin-bottom: 10px;
}
#L_S > li > p:nth-of-type(3) {
  font-weight: bold;
  font-size: 18px;
}
#L_S > li > p:last-of-type {
  font-size: 12px;
  color: #666;
}
/* 사이드배너 */
.sideBanner {
  z-index: 5;
  width: 80px;
  height: 180px;
  position: fixed;
  right: 100px;
  top: 55%;
}
.sideBanner img {
  width: 100%;
}
.sideBanner a {
  border: 1px solid #5e5e5e;
  padding: 10px 0px;
  display: block;
  text-align: center;
  background-color: #fff;
  font-size: 12px;
}
.sideBanner a:first-child {
  border: none;
  padding: 0;
}
.sideBanner a:last-of-type {
  transform: translateY(-1px);
}
