@charset "utf-8";
* {
  font-family: "Pretendard-Regular";
}
@font-face {
  font-family: "Pretendard-Regular";
  src: url("https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff")
    format("woff");
  font-weight: 400;
  font-style: normal;
}
header {
  height: 140px;
  position: relative;
  padding: 40px 0px 20px 0px;
}
h1,
header > div {
  display: inline-block;
}
h1 {
  display: none;
  /* text-align: left; */
  /* left: 20px; */
}
header > div {
  position: absolute;
  display: flex;
  gap: 20px;
  align-items: center;
}
header > div > p > a {
  color: purple;
  font-weight: bold;
}
header > div > p {
  color: gray;
}
header input {
  border: none;
  width: 300px;
}
header input:focus {
  outline: none;
}
#member {
  position: absolute;
  top: 20px;
  right: 0;
  display: flex;
  justify-content: flex-end;
  font-size: 12px;
}
#member > li:first-child::after {
  content: "|";
  display: inline-block;
}
#cate {
  position: absolute;
  display: inline-block;
  bottom: 20px;
  left: 20px;
  cursor: pointer;
  font-weight: bold;
  z-index: 1;
}
#cate:hover,
header > div > p > span:hover {
  cursor: pointer;
  color: purple;
  font-weight: bold;
}
#gnb {
  display: flex;
  justify-content: center;
  gap: 40px;
  bottom: 20px;
  position: absolute;
  width: 100%;
  font-weight: bold;
}
#gnb > li > a:hover {
  color: rgb(167, 44, 167);
  text-decoration: underline;
}
#icons {
  position: absolute;
  right: 0px;
  top: 55px;
}
#s_box {
  box-sizing: border-box;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 5px;
  border: 1px solid purple;
  display: flex;
  width: 400px;
  justify-content: space-between;
  padding-left: 20px;
}
