@charset "UTF-8";
.sub_product {
  width: 1050px;
  margin: 0 auto;
}
.sub_product h2 {
  font-size: 24px;
  text-align: center;
}
.sub_product > h2,
.sub-ul-li > p {
  margin: 40px 0px;
}
.sub-ul {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  width: 1050px;
  /* width: 1420px; */
}
.sub-ul-li {
  width: 30%;
}
.sub-ul-product_img_wrap {
  position: relative;
  height: 405.484px;
  overflow: hidden;
}
.sub-ul-cart_img {
  z-index: 3;
  position: absolute;
  right: 20px;
  bottom: 20px;
}
.sub-ul-product_img {
  transition: 0.5s;
  width: 100%;
}
.sub-ul-product_img:hover {
  transform: scale(1.03);
}
.sub-ul-p {
  line-height: 24px;
}
.sub-ul-div {
  display: flex;
  gap: 10px;
}
.sub-ul-p1 {
  color: rgb(153, 153, 153);
  font-weight: 300;
}
.sub-ul-p2 {
  font-size: 18px;
}
.sub-ul-p3 {
  color: rgb(153, 153, 153);
  font-size: 12px;
  font-weight: 300;
}
.sub-ul-p4 {
  color: red;
  font-size: 16px;
}
.sub-ul-p5 {
  font-size: 16px;
  font-weight: bold;
}
.sub-ul-p6 {
  text-decoration: line-through;
  color: rgb(181, 181, 181);
  font-size: 14px;
}
.sub-ul-p7 {
  color: rgb(153, 153, 153);
  font-size: 12px;
}
.sub-ul-p8 {
  font-size: 12px;
  background: rgb(231, 231, 231);
  padding: 2px 4px;
}
